<template>
  <view>
    <view class="linebox" @click="navto">
      <text>账户与安全</text>
      <image src="/static/image/arrow-right.png" class="arrow"></image>
    </view>
    <view class="linebox" @click="clearStorage">
      <text>清空缓存</text>
      <view style="display: flex;align-items: center;">
        <text>{{storageSize}}</text>
        <image src="/static/image/arrow-right.png" class="arrow"></image>
      </view>
    </view>
    <view class="linebox" @click="getlogin">
      <text>退出登陆</text>
      <!-- <image src="/static/image/arrow-right.png" class="arrow"></image> -->
    </view>
  </view>
</template>

<script>
  export default {
    data() {
      return {
        storageSize: ''
      }
    },
    onLoad() {
      this.getStorageSize();
    },
    methods: {
      getlogin(){
        uni.clearStorage();
        uni.reLaunch({
          url:'/pages/login/login'
        })
      },
      getStorageSize: function() {
        let that = this;
        uni.getStorageInfo({
          success(res) {
            //console.log(res.keys);
            //console.log(res.limitSize);
            let size = res.currentSize;
            if (size < 1024) {
              that.storageSize = size + ' B';
            } else if (size / 1024 >= 1 && size / 1024 / 1024 < 1) {
              that.storageSize = Math.floor(size / 1024 * 100) / 100 + ' KB';
            } else if (size / 1024 / 1024 >= 1) {
              that.storageSize = Math.floor(size / 1024 / 1024 * 100) / 100 + ' M';
            }
          }
        })
      },
      clearStorage: function() {
        let that = this;
        uni.showModal({
          title: '提示',
          content: '确定清除缓存吗?',
          confirmText: '立即清除',
          success(res) {
            if (res.confirm) {
              uni.clearStorageSync();
              //重新获取并显示清除后的缓存大小
              that.getStorageSize();
              uni.showToast({
                title: '清除成功',
                icon:'none',
                duration:2000
              })
              setTimeout(()=>{
                uni.reLaunch({
                  url:'/pages/login/login'
                })
              },2000)

            }
          }
        })
      },
      
      navto() {
        uni.navigateTo({
          url: './safety'
        })
      }
    }
  }
</script>

<style>
  page {
    background-color: #f4f6ed;
  }

  .linebox {
    background-color: #FFFFFF;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30rpx;
  }

  .arrow {
    width: 25rpx;
    height: 40rpx;
  }
</style>
